import React from 'react';
import {Link} from 'react-router-dom';

import './archive.css';

const lengthOfDate = 'XXXX-XX-XX'.length;

function getDateFromAPIHeading(apiHeading) {
  return apiHeading.slice(0, lengthOfDate);
}

function removeDateFromAPIHeading(apiHeading) {
  return apiHeading.slice(lengthOfDate);
}

function formatHeading(heading) {
  const onlyHeaderText = removeDateFromAPIHeading(heading)
  return onlyHeaderText.replace(/-/g, ' ');
}

function list(apiHeadings, resetPost) {
  return apiHeadings && apiHeadings.length > 0
    ? [...apiHeadings]
				.reverse()
				.map((heading) => (
		      <li 
		        key={heading}
		        className="archive-li"
		      >
		        <Link 
							to={ `${heading}`} 
							onClick={() => resetPost()}
							className="links"
						>
		          { getDateFromAPIHeading(heading) }
		          { ' -->' }
		          { formatHeading(heading) }
		        </Link>
		      </li>
      ))
    : '';
}

function archive({headings, emptyPost}) {
  const listOfHeadings = list(headings, emptyPost);

  return (
    <div>
      <h1>
        Archive
      </h1>
      <ul>
        { listOfHeadings }
      </ul>
    </div>
  );
}
export default archive;
