import React, {useState,useEffect} from 'react';
import {
  Switch,
  Route,
  Redirect,
  withRouter
} from 'react-router-dom';

import About from './components/about';
import Archive from './components/archive'
import Header from './components/header';
import Post from './components/post';
import Footer from './components/footer';

async function getContent(setter, url) {
  const response = await fetch(url);
  const resAsJSON = await response.json();
  setter({ 
		data: JSON.parse(resAsJSON),
		isLoading: false
	});
}


function App(props) {
  const [headings, setHeadings] = useState({ data: [], isLoading: true});
  const [post, setPost] = useState({ data: [], isLoading: true});

	const emptyPost = () => setPost({ data: [], isLoading: true});

  useEffect(() => {
    const {pathname} = props.location;
    if(headings.isLoading && ['/', '/archive'].includes(pathname)) {
      getContent(setHeadings, 'http://http://unfolding-programming.net/api/posts');
    } else if(post.isLoading && pathname !== '/about' && pathname !== 'archive') {
      getContent(setPost, `http://unfolding-programming.net/api/posts${pathname}`);
    }
  }, [post, headings, props]); 

	
  return (
    <div className="container">
      <Header />
        <Switch>
          <Route exact path="/about">
           <About />
          </Route>
          <Route exact path="/archive">
            <Archive headings={headings.data.requestedHeadings} emptyPost={emptyPost} />
          </Route>
          <Route exact path="/">
            <Redirect to="/archive" />
          </Route>
          <Route path="/:page">
            <Post post={post.data} />
          </Route>
        </Switch>
      <Footer />
    </div>
  );
}
export default withRouter(App);
